.fancy-button{
    border: 1px solid #aaaaaa;
    cursor: pointer;
    padding: .9em 1em;
    border-radius: .2em;
}
.fancy-button-default{
    background: #e6e6e6;
    color: #9e9e9e;
    &:hover{
      background-image: linear-gradient(transparent,rgba(0,0,0,.05) 40%,rgba(0,0,0,.1));
      color: #616161;
    }
}

.fancy-button-primary{
  background: #0078e7;
  color: #ecebeb;
  &:hover{
    background-image: linear-gradient(transparent,rgba(0,0,0,.05) 40%,rgba(0,0,0,.1));
    color: #ecebeb;
  }
}

.fancy-button-secondry{
    background: #1ae024;
    color: #181717;
    &:hover{
      background-image: linear-gradient(transparent,rgba(0,0,0,.05) 40%,rgba(0,0,0,.1));
      color: #181717;
    }
  }


.fancy-button-small{
    width: 200px;
}

.fancy-button-large{
    width: 400px;
}